<template>
  <Affix :offset-top="0" @on-change="change">
    <div class="nav" :class="{'isNavSolid': isNavSolid}">
      <div class="nav-wrapper">

        <div class="nav-left logo">
          <img @click="goPage('Home')" src="https://www.fitnesswithtechnique.com/wp-content/uploads/2020/09/Logo-fwt.png" height="45" width="130" alt="">
        </div>

        <div class="nav-right menu">
          <ul>
            <!-- todo: judge active item based on router -->
            <li @click="goPage('Home')" :class="{'active-menu':$route.name== 'Home'}">
              <span>HOME</span>
            </li>
            <li :class="{'active-menu':$route.name== 'FitnessCalculator'}">
              <!-- go page one page to use, use just route params to use component -->
              <Dropdown :transfer="true" transfer-class-name="with-drpdown" trigger="click" @on-click="selectCalculator">
                  <a href="javascript:void(0)">
                      FITNESS CALCULATOR
                  </a>
                  <DropdownMenu slot="list">
                      <DropdownItem style="border-bottom: 1px solid #E6E6E6;" name="0" >Body Mass Index</DropdownItem>
                      <!-- <DropdownItem style="border-bottom: 1px solid #E6E6E6;">Body Fat Index</DropdownItem>
                      <DropdownItem style="border-bottom: 1px solid #E6E6E6;">Calories Burned</DropdownItem>
                      <DropdownItem style="border-bottom: 1px solid #E6E6E6;">Daily Calorie Intake</DropdownItem>
                      <DropdownItem>One-Rep Max</DropdownItem> -->
                  </DropdownMenu>
              </Dropdown>
            </li>
            <li @click="goPage('Exercises')" :class="{'active-menu':$route.name== 'Exercises'}">
              <span>EXERCISES</span>
            </li>
            <li @click="goPage('Nutrition')" :class="{'active-menu':$route.name== 'Nutrition'}">
              <span>NUTRITION</span>
            </li>
            <li @click="goPage('AboutUs')" :class="{'active-menu':$route.name== 'AboutUs'}">
              <span>ABOUT</span>
            </li>
            <li @click="goPage('ContactUs')" :class="{'active-menu':$route.name== 'ContactUs'}">
              <span>CONTACT US</span>
            </li>
            <li class="poptip">
              <Poptip title="" content="content" placement="bottom-end">
                  <Icon size="24" type="md-search" />
                  <template #content>
                    <Input v-model="search" placeholder="Search the site..." style="width: auto">
                        <Icon type="ios-search" slot="suffix" />
                    </Input>
                    <Divider v-if="search"/>
                    <div class="results" v-if="search">
                      <div class="no-results">
                        <small>No results</small>
                      </div>
                    </div>
                  </template>
              </Poptip>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </Affix>
</template>

<script>
export default {
    data() {
      return {
        currPage: "Home",
        search: "",
        isNavSolid: false,
      };
    },
    watch: {
      $route(route) {
        // console.log(route);
      },
    },
    methods: {
        selectCalculator(e) {
          this.goPage("FitnessCalculator", e)
        },
        goPage(name, params) {
          if (name == this.$route.name) return;
          if (params) {
            this.$router.push({
              name: name,
              params: {
                name: params
              },
            })
          } else {
            this.$router.push({name: name})
          }
        },
        detectScroll() {
          const currentRec = document.documentElement.getBoundingClientRect();
          if (currentRec.y < -65) {
            this.isNavSolid = true;
          } else {
            this.isNavSolid = false;
          }
        },
        change (status) {
            // console.log(status);
            // this.$Message.info(`Status: ${status}`);
        }
    },
    mounted() {
      window.addEventListener('scroll', this.detectScroll, true);
    },
};
</script>

